.header {
	margin-top: 10px;
	font-family: 'Lato', sans-serif;
	font-size: 36px;
	font-weight: 900;
	color: #dbdbdb;
	text-align: center;
	text-shadow: 2px 2px 2px #000;
	
	@media (max-width: 560px) {
		font-size: 36px;
	}
	
	@media (max-width: 460px) {
		margin: 5px 0;
		font-size: 30px;
	}
}